@supports (-webkit-appearance: none) or (-moz-appearance: none) {
  input[type=checkbox],
input[type=radio] {
    --selection-input-size: 1rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: var(--selection-input-size);
    height: var(--selection-input-size);
    border: 2px solid var(--selection-input-border-color, #e2e2e7);
    background-color: var(--selection-input-background, #fff);
    outline: 1px solid transparent;
    margin: 0 4px 0 0;
    transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, outline 0.15s ease-in-out;
    vertical-align: middle;
    cursor: pointer;
    position: relative;
  }
  input[type=checkbox]:hover,
input[type=radio]:hover {
    --selection-input-border-color: #ff888b;
  }
  input[type=checkbox]:checked,
input[type=radio]:checked {
    --selection-input-background: #da2530;
    --selection-input-border-color: #da2530;
    --selection-input-inner-color: #fff;
  }
  input[type=checkbox]:focus,
input[type=radio]:focus {
    outline: 2px solid #71adff;
  }
  input[type=checkbox]::after,
input[type=radio]::after {
    content: "";
    transform-origin: center;
    transition: background-color 0.15s cubic-bezier(0, 1, 0.4, 1), width 0.15s cubic-bezier(0, 1, 0.4, 1), height 0.15s cubic-bezier(0, 1, 0.4, 1);
    position: absolute;
  }
  input[type=checkbox] + label,
input[type=radio] + label {
    cursor: pointer;
    vertical-align: middle;
    font-weight: 400;
  }

  input[type=checkbox]:checked {
    --selection-input-inner-size: 70%;
  }
  input[type=checkbox]::after {
    transform: translate(-50%, -50%) rotate(45deg);
    left: 50%;
    top: 50%;
    width: calc(var(--selection-input-inner-size, 1px) / 2);
    height: var(--selection-input-inner-size, 0);
    border: 2px solid var(--selection-input-inner-color, transparent);
    border-left: none;
    border-top: none;
  }

  input[type=radio] {
    border-radius: var(--selection-input-size);
  }
  input[type=radio]:checked {
    --selection-input-inner-size: 50%;
  }
  input[type=radio]::after {
    width: var(--selection-input-inner-size, 0);
    height: var(--selection-input-inner-size, 0);
    background-color: var(--selection-input-inner-color, transparent);
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    border-radius: 50%;
  }
}
select {
  appearance: none;
  border: none;
  font-family: Inter, system-ui, sans-serif;
  font-size: 0.875rem;
  padding: 8px 16px;
  background-color: rgba(0, 0, 0, 0.03);
  width: 100%;
  height: 40px;
  transition: background-color 0.15s ease-in-out;
  background-image: url("../images/icons/chevron-down.svg");
  background-position: 98% center;
  background-size: auto 40%;
  background-repeat: no-repeat;
}
select:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
select:focus {
  background-color: #fff;
}